<template>
	<div>
		<div class="title"></div>
		<div class="searchs">
			<label for="search_content">
				<el-input id="search_content" v-model="search_content" placeholder=""></el-input>
			</label>
			<el-button class="mr25" type="primary" icon="el-icon-search"></el-button>
			<label for="is_show">
				<el-select v-model="is_show" placeholder="">
					<el-option
						id="is_show"
						v-for="item in is_shows"
						:key="item"
						:value="item">
					</el-option>
				</el-select>
			</label>
			<el-button type="success" class="add_btn" @click="dialog_show=true;dialog_type=''"></el-button>
		</div>
		<table class="main_table">
			<thead>
				<tr>
					<td>ID</td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
			</thead>
			<tbody>
				<tr v-for="item in 10">
					<td>1000001</td>
					<td></td>
					<td></td>
					<td>2018-11-16 17:51:20</td>
					<td></td>
					<td><el-button size="mini" type="warning" icon="el-icon-edit" @click="dialog_show=true;dialog_type=''"></el-button></td>
				</tr>
			</tbody>
			<tfoot>
				<tr>
					<td colspan="11">
						<el-pagination
							background
							layout="prev, pager, next"
							:total="1000">
						</el-pagination>
					</td>
				</tr>
			</tfoot>
		</table>
		<el-dialog
			title=""
			:visible.sync="dialog_show"
			width="30%"
			center>
			<table class="mini">
				<caption>{{dialog_type}}</caption>
				<tr>
					<td></td>
					<td><el-input v-model="game_name" placeholder=""></el-input></td>
				</tr>
				<tr>
					<td></td>
					<td><el-input v-model="game_newDept" placeholder=""></el-input></td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-date-picker
							v-model="start_time"
							type="datetime"
							placeholder="">
						</el-date-picker>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-select v-model="is_show_dialog" placeholder="">
							<el-option
								id="is_show"
								v-for="item in is_shows"
								:key="item"
								:value="item">
							</el-option>
						</el-select>
					</td>
				</tr>
			</table>
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" @click="submit"></el-button>
				<el-button @click="dialog_show = false"></el-button>
			</span>
		</el-dialog>
	</div>
</template>
<script>
export default{
	data(){
		return{
			search_content:'',//
			is_show:'',//
			is_show_dialog:'',//
			is_shows:['',''],//
			dialog_show:false,//
			dialog_type:'',//
			game_name:'',// 
			game_newDept:'',//
			start_time:'',//
		}
	},
	methods:{
		submit(){
			this.dialog_show=false;
			var str=this.dialog_type.substring(0,2);
			this.$message({
	          	message: `${str}`,
	          	type: 'success'
	        });
		}
	}
}
</script>
<style scoped lang="styl">

</style>


