<template>
	<div>
		<div class="title"></div>
		<div class="searchs">
			<label for="search_content">
				<el-input id="search_content" v-model="search_content" placeholder=""></el-input>
			</label>
			<el-button class="mr25" type="primary" icon="el-icon-search"></el-button>
			<label for="time">
				<el-date-picker
					id="time"
					class="bothdate"
			    	v-model="time"
			    	type="datetimerange"
			    	range-separator=""
			    	start-placeholder=""
			    	end-placeholder="">
			    </el-date-picker>
			</label>
			<el-button type="success" class="add_btn" @click="dialog_show=true;dialog_type=''"></el-button>
		</div>
		<table class="main_table">
			<thead>
				<tr>
					<td>ID</td>
					<td></td>
					<td>ID</td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
			</thead>
			<tbody>
				<tr v-for="item in 10">
					<td>1000001</td>
					<td></td>
					<td>1000001</td>
					<td>hello world</td>
					<td>2018-11-14 16:29:30</td>
					<td><el-button size="mini" type="warning" icon="el-icon-edit" @click="dialog_show=true;dialog_type=''"></el-button></td>
				</tr>
			</tbody>
			<tfoot>
				<tr>
					<td colspan="11">
						<el-pagination
							background
							layout="prev, pager, next"
							:total="1000">
						</el-pagination>
					</td>
				</tr>
			</tfoot>
		</table>
		<el-dialog
			title=""
			:visible.sync="dialog_show"
			width="30%"
			center>
			<table class="mini">
				<caption>{{dialog_type}}</caption>
				<tr v-if="dialog_type==''">
					<td>ID</td>
					<td><el-input :disabled="dialog_type==''" v-model="message_id" placeholder="ID"></el-input></td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-select :disabled="dialog_type==''" v-model="message_type" placeholder="">
							<el-option
								v-for="item in message_types"
								:key="item"
								:value="item">
							</el-option>
						</el-select>
					</td>
				</tr>
				<tr>
					<td>ID</td>
					<td><el-input :disabled="dialog_type==''" v-model="user_id" placeholder="ID"></el-input></td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-input
							class="textarea"
							:disabled="dialog_type==''"
							type="textarea"
							:rows="2"
							placeholder=""
							v-model="message_content">
						</el-input>
					</td>
				</tr>
			</table>
			<span slot="footer" class="dialog-footer">
				<el-button v-if="dialog_type==''" type="primary" @click="submit"></el-button>
				<el-button @click="dialog_show = false"></el-button>
			</span>
		</el-dialog>
	</div>
</template>
<script>
export default{
	data(){
		return{
			search_content:'',//
			time:'',//
			dialog_show:false,//
			dialog_type:'',//
			message_id:'',//ID
			message_type:'',//
			message_types:['',''],//
			user_id:'',//ID
			message_content:'',//
		}
	},
	methods:{
		submit(){
			this.dialog_show=false;
			var str=this.dialog_type.substring(0,2);
			this.$message({
	          	message: `${str}`,
	          	type: 'success'
	        });
		}
	}
}
</script>
<style scoped lang="styl">

</style>


