<template>
	<div>
		<div class="title"></div>
		<div class="searchs">
			<div>
				<label for="gameType">
					<el-select clearable v-model="gameTypeSearch" placeholder="">
						<el-option
							id="gameType"
							v-for="item in gameTypes"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="isHotGame">
					<el-select clearable v-model="isHotGameSearch" placeholder="">
						<el-option
							id="isHotGame"
							v-for="item in isHotGames"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="gameClass">
					<el-select clearable v-model="gameClassSearch" placeholder="">
						<el-option
							id="gameClass"
							v-for="item in gameClasses"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="hotTag">
					<el-select clearable v-model="hotTagsSearch" placeholder="">
						<el-option
							id="hotTag"
							v-for="item in hotTags"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="isShow">
					<el-select clearable v-model="isShowSearch" placeholder="">
						<el-option
							id="isShow"
							v-for="item in isShows"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
			</div>
			<div class="mt25">
				<label for="searchType">
					<el-select clearable v-model="searchType" placeholder="">
						<el-option
							id="searchType"
							v-for="item in searchTypes"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="searchContent">
					<el-input clearable id="searchContent" v-model="searchContent" placeholder=""></el-input>
				</label>
				<label for="time">
					<el-date-picker
						id="time"
						class="bothdate"
				    	v-model="time"
				    	type="datetimerange"
				    	range-separator=""
				    	start-placeholder=""
				    	end-placeholder=""
				    	value-format="yyyy-MM-dd HH:mm:ss">
				    </el-date-picker>
				</label>
				<el-button class="mr25" type="primary" icon="el-icon-search" @click="search"></el-button>
				<el-button type="success" class="add_btn" @click="add"></el-button>
			</div>
		</div>
		<table class="main_table">
			<thead>
				<tr>
					<td>ID</td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(item,index) in gameList">
					<td>{{item.gameId}}</td>
					<td>{{item.gameName}}</td>
					<td>{{item.gameType}}</td>
					<td>{{item.isHot}}</td>
					<td>{{item.gameClass}}</td>
					<td>{{item.hotTag}}</td>
					<td>{{item.operateName}}</td>
					<td>{{item.onlineTime}}</td>
					<td>{{item.isShow}}</td>
					<td><el-button size="mini" type="warning" icon="el-icon-edit" @click="modify"></el-button></td>
				</tr>
			</tbody>
			<tfoot>
				<tr>
					<td colspan="11">
						<el-pagination
							background
							layout="prev, pager, next"
							:total="pages*10">
						</el-pagination>
					</td>
				</tr>
			</tfoot>
		</table>
		<!--  -->
		<el-dialog
			title=""
			:visible.sync="dialogShow"
			width="60%"
			center>
			<table class="mini">
				<caption>{{dialogType}}</caption>
				<tr>
					<td></td>
					<td><el-input clearable v-model="gameName" placeholder=""></el-input></td>
				</tr>
				<tr>
					<td></td>
					<td>
						<input id="game_icon" type="file" @change="change_file_img('game_icon','select_img1')">
						<img v-if="dialogType==''" class="select_img1" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_icon')">
						<img v-if="dialogType==''" class="select_img1" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_icon')">
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-select multiple clearable v-model="gameType" placeholder="">
					    	<el-option
					    		v-for="item in gameTypes"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-select clearable v-model="isHotGame" placeholder="">
					    	<el-option
					    		v-for="item in isHotGames"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-select clearable v-model="gameClass" placeholder="">
					    	<el-option
					    		v-for="item in gameClasses"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr v-if="gameClass==''">
					<td></td>
					<td>
						<el-input clearable v-model="gameLink" placeholder=""></el-input>
					</td>
				</tr>
				<tr v-if="gameClass==''">
					<td></td>
					<td>
						<input id="game_ewm" type="file" @change="change_file_img('game_ewm','select_img2')">
						<img v-if="dialogType==''" class="select_img2" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_ewm')">
						<img v-if="dialogType==''" class="select_img2" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_ewm')">
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-select multiple clearable v-model="hotTag" placeholder="">
					    	<el-option
					    		v-for="item in hotTags"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-input clearable v-model="saleDepartment" placeholder=""></el-input>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<label clearable for="showTime">
							<el-date-picker
								id="showTime"
								v-model="showTime"
								type="datetime"
								placeholder="">
							</el-date-picker>
						</label>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-input clearable disabled v-model="onlinePeoples" placeholder=""></el-input>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-select clearable v-model="isShow" placeholder="">
					    	<el-option
					    		v-for="item in isShows"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<input id="game_mini" type="file" @change="change_file_img('game_mini','select_img3')">
						<img v-if="dialogType==''" class="select_img3" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_mini')">
						<img v-if="dialogType==''" class="select_img3" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_mini')">
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-input
							class="textarea"
							type="textarea"
							:rows="2"
							placeholder=""
							v-model="gameIntroduce">
						</el-input>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-input
							class="textarea"
							type="textarea"
							:rows="2"
							placeholder=""
							v-model="skillLighter">
						</el-input>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<input id="game_banner" type="file" @change="change_file_img('game_banner','select_img4')">
						<img v-if="dialogType==''" class="select_img4" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_banner')">
						<img v-if="dialogType==''" class="select_img4" src="static/games_list/add.png" alt="hello world" @click="pic_click('game_banner')">
					</td>
				</tr>
			</table>
			<table class="mini">
				<tbody>
					<!--  -->
					<tr>
						<td></td>
						<td colspan="2"></td>
						<td></td>
					</tr>
					<tr v-for="(item,index) in featureGameplay">
						<td>
							<el-input clearable v-model="featureGameplay[index].title" placeholder=""></el-input>
						</td>
						<td colspan="2">
							<el-input clearable v-model="featureGameplay[index].link" placeholder=""></el-input>
						</td>
						<td>
							<el-button type="success" @click="public_add('featureGameplay',index)"></el-button>
							<el-button type="danger" @click="public_del('featureGameplay',index)"></el-button>
						</td>
					</tr>
					<!--  -->
					<tr>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
					</tr>
					<tr v-for="(item,index) in roleInfos" class="roleInfos">
						<td>
							<input :id="`game_infoImg${index}`" type="file" @change="change_file_img(`game_infoImg${index}`,`select_imga_${index}`)">
							<img v-if="dialogType==''" :class="`select_imga_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_infoImg${index}`)">
							<img v-if="dialogType==''" :class="`select_imga_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_infoImg${index}`)">
							<!-- <input type="file" style="display:block"> -->
						</td>
						<td>
							<el-input clearable v-model="roleInfos[index].name" placeholder=""></el-input>
						</td>
						<td>
							<el-input
								class="input-mini"
								type="textarea"
								:rows="2"
								placeholder=""
								v-model="roleInfos[index].description">
							</el-input>
						</td>
						<td>
							<el-button type="success" @click="public_add('roleInfos',index)"></el-button>
							<el-button type="danger" @click="public_del('roleInfos',index)"></el-button>
						</td>
					</tr>
					<!--  -->
					<tr>
						<td></td>
						<td colspan="2"></td>
						<td></td>
					</tr>
					<tr v-for="(item,index) in gameWallpaper" class="gameWallpaper">
						<td>
							<input :id="`game_pic_${index}`" type="file" @change="change_file_img(`game_pic_${index}`,`select_imgb_${index}`)">
							<img v-if="dialogType==''" :class="`select_imgb_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_pic_${index}`)">
							<img v-if="dialogType==''" :class="`select_imgb_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_pic_${index}`)">
						</td>
						<td colspan="2">
							<input :id="`game_pic_l_${index}`" type="file" @change="change_file_img(`game_pic_l_${index}`,`select_imgb_l_${index}`)">
							<img v-if="dialogType==''" :class="`select_imgb_l_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_pic_l_${index}`)">
							<img v-if="dialogType==''" :class="`select_imgb_l_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`game_pic_l_${index}`)">
						</td>
						<td>
							<el-button type="success" @click="public_add('gameWallpaper',index)"></el-button>
							<el-button type="danger" @click="public_del('gameWallpaper',index)"></el-button>
						</td>
					</tr>
					<!--  -->
					<tr>
						<td></td>
						<td colspan="2"></td>
						<td></td>
					</tr>
					<tr v-for="(item,index) in gameScreenshot" class="gameScreenshot">
						<td>
							<input :id="`gameScreenshot${index}`" type="file" @change="change_file_img(`gameScreenshot${index}`,`select_imgc_${index}`)">
							<img v-if="dialogType==''" :class="`select_imgc_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`gameScreenshot${index}`)">
							<img v-if="dialogType==''" :class="`select_imgc_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`gameScreenshot${index}`)">
						</td>
						<td colspan="2">
							<input :id="`gameScreenshot_l_${index}`" type="file" @change="change_file_img(`gameScreenshot_l_${index}`,`select_imgc_l_${index}`)">
							<img v-if="dialogType==''" :class="`select_imgc_l_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`gameScreenshot_l_${index}`)">
							<img v-if="dialogType==''" :class="`select_imgc_l_${index}`" src="static/games_list/add.png" alt="hello world" @click="pic_click(`gameScreenshot_l_${index}`)">
						</td>
						<td>
							<el-button type="success" @click="public_add('gameScreenshot',index)"></el-button>
							<el-button type="danger" @click="public_del('gameScreenshot',index)"></el-button>
						</td>
					</tr>
				</tbody>
			</table>
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" @click="submit"></el-button>
				<el-button @click="dialogShow = false"></el-button>
			</span>
		</el-dialog>
	</div>
</template>
<style scoped lang="styl">
</style>
<script>
export default{
	data(){
		return{
			gameList:[],//
			page:1,//
			pages:0,//
			time:'',//
			gameTypeSearch:'',//
			isHotGameSearch:'',//
			gameClassSearch:'',//
			hotTagsSearch:'',//
			isShowSearch:'',//
			searchType:'',//
			searchTypes:['',''],//
			searchContent:'',//
			dialogType:'',//
			dialogShow:false,// 
			gameName:'',//
			gameType:[],//
			gameTypes:['','','','','','','','','','',''],//
			isHotGame:'',//
			isHotGames:['',''],//
			gameClass:'',//
			gameClasses:['',''],//
			gameLink:'',//
			hotTag:[],//
			hotTags:['',''],//
			saleDepartment:'',//
			showTime:'',//
			onlinePeoples:'',//
			isShow:'',//
			isShows:['',''],//,
			gameIntroduce:'',//
			skillLighter:'',//
			featureGameplay:[
				{
					title:'',//
					link:''//
				}
			],//
			roleInfos:[
				{
					name:'',//
					description:''//
				}
			],//
			gameWallpaper:[0],//
			gameScreenshot:[0],//
			textarea:''
		}
	},
	created(){
		this.getGameList();
	},
	methods:{
		// 
		getGameList(){
			this.$axios.get(`/infos/game_list_get
				?page=${this.page}
				&gameType=${this.gameTypeSearch}
				&isHot=${this.isHotGameSearch}
				&gameClass=${this.gameClassSearch}
				&hotTag=${this.hotTagsSearch}
				&isShow=${this.isShowSearch}
				&searchType=${this.searchType}
				&searchContent=${this.searchContent}
				&time=${this.time}
				`).then((res)=>{
				this.gameList=res.data.rows;
				this.pages=res.data.pages;
			}).catch((err)=>{
				console.log(err);
			});
		},
		// 
		search(){
			// 
			if(this.searchType==''&&this.searchContent!=''){
				this.$message({
		          	message: ``,
		          	type: 'warning'
		        });
			}else{
				this.getGameList();
			}
		},
		// 
		add(){
			// 
			this.dialogShow=true;
			this.dialogType='';
			// 
			this.modal_init();
		},
		// 
		modify(){
			this.dialogShow=true;
			this.dialogType='';
			// 
			this.modal_init();
		},
		// 
		modal_init(){
			// 
			$('img').attr('src','static/games_list/add.png');
			$('input:file').val('');
			this.gameName='';
			this.gameType=[];
			this.isHotGame='';
			this.gameClass='';
			this.gameLink='';
			this.hotTag=[];
			this.saleDepartment='';
			this.showTime='';
			this.onlinePeoples='';
			this.isShow='';
			this.gameIntroduce='';
			this.skillLighter='';
			this.featureGameplay=[{title:'',link:''}];
			this.roleInfos=[{name:'',description:''}];
		},
		// 
		public_add(source,index){
			switch(source){
				case 'featureGameplay':var obj={title:'',link:''};break;
				case 'roleInfos':var obj={name:'',description:''};break;
				case 'gameWallpaper':var obj={};break;
			}
			this[source].splice(index+1,0,obj);
		},
		// /
		public_del(source,index){
			if(source=='gameWallpaper'){
				$(`#game_pic_${index}`).val('');
				$(`#game_pic_l_${index}`).val('');
				$(`.select_imgb_${index}`).attr('src','static/games_list/add.png');
				$(`.select_imgb_l_${index}`).attr('src','static/games_list/add.png');
			}else if(source=='gameScreenshot'){
				$(`#gameScreenshot_${index}`).val('');
				$(`#gameScreenshot_l_${index}`).val('');
				$(`.select_imgc_${index}`).attr('src','static/games_list/add.png');
				$(`.select_imgc_l_${index}`).attr('src','static/games_list/add.png');
			}else if(source=='roleInfos'){
				$(`#game_infoImg${index}`).val('');
				$(`.select_imga_${index}`).attr('src','static/games_list/add.png');
				this.roleInfos[index].name='';
				this.roleInfos[index].description='';
			}else{
				if(this[source].length>1){
					this[source].splice(index,1);
				}else{
					this.$message({
			        	message: ',',
			        	type: 'warning'
			        });
				}
			}
		},
		// 
		submit(){
			this.dialogShow=false;
			// --
			this.on_click_upload_file('game_icon','icon');
			if(this.gameClass==''){
				// --
				this.on_click_upload_file('game_ewm','ewm');
			}
			//--
			this.on_click_upload_file('game_mini','mini');
			// --
			this.on_click_upload_file('game_banner','background');
			//--
			var length=$('.roleInfos').length;
			for(var i=0;i<length;i++){
				this.on_click_upload_file(`game_infoImg${i}`,'role');
			}
			// --
			var length=$('.gameWallpaper').length;
			for(var i=0;i<length;i++){
				this.on_click_upload_file(`game_pic_${i}`,'wallpaper');
				this.on_click_upload_file(`game_pic_l_${i}`,'wallpaper_large');
			}
			// --
			var length=$('.gameScreenshot').length;
			for(var i=0;i<length;i++){
				this.on_click_upload_file(`gameScreenshot${i}`,'screenshot');
				this.on_click_upload_file(`gameScreenshot_l_${i}`,'screenshot_large');
			}
			var str=this.dialogType.substring(0,2);
			this.$message({
	          	message: `${str}`,
	          	type: 'success'
	        });
		},
		// 
		pic_click(id){
			$(`#${id}`).click();
		},
		// 
		change_file_img(_id,type){
			var preview=document.querySelectorAll(`.${type}`)[0];
			var file=document.getElementById(_id).files[0];
			var reader=new FileReader();
			reader.onloadend=()=>{
                preview.src=reader.result;
            }
            if(file){
                reader.readAsDataURL(file);
            }
		},
		// ali-oss
		on_click_upload_file(file_id,file_path){
			// 
			var client = new OSS.Wrapper({
		        region: 'oss-cn-shenzhen',
		        accessKeyId: 'LTAIaGX9gQ9iRNyJ',
		        accessKeySecret: '4NoCWvII2zAdM3xFNCFjNxxjzeILbb',
		        bucket:'brand-new-days'
		    });
			var f = document.getElementById(file_id).files[0];
	    	var val= document.getElementById(file_id).value;
	  		// 
	    	if(val==''){
				return;
			}
	    	var obj=`${file_path}/${f.name}`;  // 
	    	var storeAs = obj;  //,/ (crate)
	    	console.log(' => ' + storeAs);
	    	client.multipartUpload(storeAs, f).then(function (result) {
	    		console.log(result);
	    		console.log(result.url);
	    	}).catch(function (err) {
	    	    console.log(err);
	    	});
		}
	}
}
</script>


