<template>
	<div>
		<div class="title"></div>
		<div class="searchs">
			<div>
				<label for="time">
					<el-date-picker
						id="time"
						class="bothdate"
				    	v-model="time"
				    	type="datetimerange"
				    	range-separator=""
				    	start-placeholder=""
				    	end-placeholder=""
				    	value-format="yyyy-MM-dd HH:mm:ss">
				    </el-date-picker>
				</label>
				<label for="loginType">
					<el-select clearable v-model="loginType" placeholder="">
						<el-option
							id="loginType"
							v-for="item in loginTypes"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="userState">
					<el-select clearable v-model="userState" placeholder="">
						<el-option
							id="userState"
							v-for="item in userStates"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
			</div>
			<div class="mt25">
				<label for="searchType">
					<el-select clearable v-model="searchType" placeholder="">
						<el-option
							id="searchType"
							v-for="item in searchTypes"
							:key="item"
							:value="item">
						</el-option>
					</el-select>
				</label>
				<label for="searchContent">
					<el-input clearable id="searchContent" v-model="searchContent" placeholder=""></el-input>
				</label>
				<el-button class="mr25" type="primary" icon="el-icon-search" @click="search"></el-button>
			</div>
		</div>
		<table class="main_table">
			<thead>
				<tr>
					<td>ID</td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
			</thead>
			<tbody>
				<tr v-for="item in userList">
					<td>{{item.userId}}</td>
					<td>{{item.userAccount}}</td>
					<td>{{item.loginType}}</td>
					<td>{{item.userName}}</td>
					<td>{{item.phone}}</td>
					<td>{{item.sex}}</td>
					<td>{{item.brith}}</td>
					<td>{{item.money}}</td>
					<td>{{item.registerTime|filter_time}}</td>
					<td>{{item.userState}}</td>
					<td><el-button size="mini" type="primary" icon="el-icon-search" @click="look_modal(item)"></el-button></td>
				</tr>
			</tbody>
			<tfoot>
				<tr>
					<td colspan="11">
						<el-pagination
							background
							layout="prev, pager, next"
							:total="pages*10"
							@current-change="change_page">
						</el-pagination>
					</td>
				</tr>
			</tfoot>
		</table>
		<el-dialog
			title=""
			:visible.sync="dialogShow"
			width="30%"
			center>
			<table>
				<caption></caption>
				<tr>
					<td>ID</td>
					<td>{{lookPro.userId}}</td>
				</tr>
				<tr>
					<td></td>
					<td>{{lookPro.userAccount}}</td>
				</tr>
				<tr>
					<td></td>
					<td>{{lookPro.userName}}</td>
				</tr>
				<tr>
					<td></td>
					<td>{{lookPro.loginType}}</td>
				</tr>
				<tr>
					<td></td>
					<td>{{lookPro.phone}}</td>
				</tr>
				<tr>
					<td></td>
					<td>{{lookPro.sex}}</td>
				</tr>
				<tr>
					<td></td>
					<td>{{lookPro.money}}</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-select v-model="userStateDefault" disabled placeholder="">
					    	<el-option
					    		v-for="item in userStates"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
			</table>
			<table>
				<caption class="cap2"></caption>
				<tr>
					<td></td>
					<td>
						<el-select clearable v-model="userStateChange" placeholder="">
					    	<el-option
					    		v-for="item in userStates"
					    		:key="item"
					    		:value="item">
					    	</el-option>
					  	</el-select>
					</td>
				</tr>
				<tr>
					<td colspan="2"><el-button type="primary" @click="submit"></el-button></td>
				</tr>
			</table>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogShow = false"></el-button>
			</span>
		</el-dialog>
	</div>
</template>
<style scoped lang="styl">
</style>
<script>
export default{
	created(){
		this.getUserList();//
	},
	data(){
		return{
			userList:[],//
			lookPro:[],//
			page:1,//
			pages:0,//
			searchType:'',//
			searchTypes:['','',''],//
			searchContent:'',//
			time:'',//
			loginType:'',//
			loginTypes:['','QQ','','','',''],//
			userState:'',//--
			userStates:['',''],//
			userStateChange:'',//--
			dialogShow:false,// /
			userStateDefault:'',// --
		}
	},
	filters:{
		filter_time(value){
			// 
			var date=value.split('T')[0];
			var time=value.split('T')[1].split('.')[0];
			return date+' '+time;
		}
	},
	methods:{
		getUserList(){
			// 
			this.$axios.get(`/infos/user_list_get
				?page=${this.page}
				&loginType=${this.loginType}
				&userState=${this.userState}
				&time=${this.time}
				&searchType=${this.searchType}
				&searchContent=${this.searchContent}
				`).then((res)=>{
				this.userList=res.data.rows;
				this.pages=res.data.pages;
			}).catch((err)=>{
				console.log(err);
			});
		},
		// 
		search(){
			// 
			if(this.searchType==''&&this.searchContent!=''){
				this.$message({
		          	message: ``,
		          	type: 'warning'
		        });
			}else{
				this.getUserList();
			}
		},
		// 
		change_page(page){
			// 
			this.page=page;
			this.getUserList();
		},
		// 
		look_modal(item){
			// 
			this.lookPro=item;
			// 
			this.userStateDefault=item.userState;
			// 
			this.userStateChange='';
			this.dialogShow=true;
		},
		// 
		submit(){
			// 
			if(this.userStateChange==''){
				this.$message({
		          	message: ``,
		          	type: 'warning'
		        });
			}else{
				// 
				this.$axios.post('/infos/user_list_post',this.$qs.stringify({
					userId:this.lookPro.userId,
					state:this.userStateChange
				})).then((res)=>{
					// 
					if(res.data.code!=0){
						this.$message({
				          	message: `${res.data.type}`,
				          	type: 'error'
				        });
					}else{
					// 
						this.dialogShow=false;
						this.$message({
				          	message: `${res.data.type}`,
				          	type: 'success'
				        });
				        // 
				        this.getUserList();
					}
				}).catch((err)=>{
					console.log(err);
				});
			}
		}
	}
}
</script>


