<template>
	<div>
		<div class="title"></div>
		<div class="searchs">
			<label for="search_content">
				<el-input id="search_content" v-model="search_content" placeholder=""></el-input>
			</label>
			<el-button class="mr25" type="primary" icon="el-icon-search"></el-button>
			<label for="all_state">
				<el-select v-model="all_state" placeholder=" ">
					<el-option
						id="all_state"
						v-for="item in all_states"
						:key="item"
						:value="item">
					</el-option>
				</el-select>
			</label>
			<label for="time">
				<el-date-picker
					id="time"
					class="bothdate"
			    	v-model="time"
			    	type="datetimerange"
			    	range-separator=""
			    	start-placeholder=""
			    	end-placeholder="">
			    </el-date-picker>
			</label>
		</div>
		<table class="main_table">
			<thead>
				<tr>
					<td>ID</td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
					<td></td>
				</tr>
			</thead>
			<tbody>
				<tr v-for="item in 10">
					<td>1000001</td>
					<td></td>
					<td>xiaobai</td>
					<td>132132231321</td>
					<td>2018-11-16 17:51:20</td>
					<td></td>
					<td><el-button size="mini" type="primary" icon="el-icon-search" @click="dialog_show=true"></el-button></td>
				</tr>
			</tbody>
			<tfoot>
				<tr>
					<td colspan="11">
						<el-pagination
							background
							layout="prev, pager, next"
							:total="1000">
						</el-pagination>
					</td>
				</tr>
			</tfoot>
		</table>
		<el-dialog
			title=""
			:visible.sync="dialog_show"
			width="30%"
			center>
			<table class="mini">
				<caption></caption>
				<tr>
					<td></td>
					<td><el-input disabled v-model="company_name" placeholder=""></el-input></td>
				</tr>
				<tr>
					<td></td>
					<td><el-input disabled v-model="username" placeholder=""></el-input></td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-input disabled v-model="telphone" placeholder=""></el-input>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-input
							class="textarea"
							disabled
							type="textarea"
							:rows="2"
							placeholder=""
							v-model="comment">
						</el-input>
					</td>
				</tr>
				<tr>
					<td></td>
					<td>
						<el-radio v-model="is_read" label="1"></el-radio>
  						<el-radio v-model="is_read" label="2"></el-radio>
					</td>
				</tr>
			</table>
			<span slot="footer" class="dialog-footer">
				<el-button type="primary" @click="submit"></el-button>
				<el-button @click="dialog_show = false"></el-button>
			</span>
		</el-dialog>
	</div>
</template>
<script>
export default{
	data(){
		return{
			search_content:'',//
			all_state:'',//
			all_states:['',''],//
			time:'',//
			dialog_show:false,//
			company_name:'',// 
			username:'',//
			telphone:'',//
			comment:'',//
			is_read:''//
		}
	},
	methods:{
		submit(){
			this.dialog_show=false;
			this.$message({
	          	message: ``,
	          	type: 'success'
	        });
		}
	}
}
</script>
<style scoped lang="styl">

</style>


